import Vue from 'vue'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'

import {jssdk} from './api'
Vue.use(WechatPlugin)
const wxShare = ()=>{
var title = '发亮摔跤俱乐部';
var link = `${location.origin}${location.pathname}`;
var desc = '发亮摔跤俱乐部少儿体适能招生中,搭建摔跤MMA综合格斗成人及少儿培训行业最大的共赢平台';
var imgUrl = 'https://mmbiz.qpic.cn/mmbiz_jpg/SlsNDlY61uOT3PN3zX6iaHibtnMyy5PIsdXl10nF2J1iaujl0hEC5Mw7ebebkQ9DcKaJOFiaI8mvNXbaZW7hBUvduQ/640?wx_fmt=jpeg&tp=webp&wxfrom=5&wx_lazy=1&wx_co=1';
















Vue.wechat.config({
debug: false,
appId:'wxd9a823579903ebc1',
timestamp:'1539164551',
nonceStr:'EqQwcRnIOmvofOOc',
signature:'fe93094ac1199f5fb0b88ecb3d7f6d47b2bec81f',
jsApiList:[
'onMenuShareTimeline',//分享到朋友圈
'onMenuShareAppMessage',//分享给朋友
'onMenuShareQQ',//分享到QQ
'onMenuShareQZone',//分享到QQ空间
'onMenuShareWeibo'
]
})
Vue.wechat.ready(function(){

Vue.wechat.onMenuShareTimeline({
title:title, // 分享标题
link: link, // 分享链接
desc: desc, // 分享描述
imgUrl:imgUrl, // 分享图标
success: function () {


},
cancel: function () {

}
});

Vue.wechat.onMenuShareAppMessage({
title: title, // 分享标题
desc: desc, // 分享描述
link: link, // 分享链接
imgUrl: imgUrl, // 分享图标
type: '', // 分享类型,music、video或link，不填默认为link
dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
success: function () {


},
cancel: function () {

}
});
Vue.wechat.onMenuShareQQ({
title: title, // 分享标题
desc: desc, // 分享描述
link: link, // 分享链接
imgUrl: imgUrl, // 分享图标
success: function () {


},
cancel: function () {

}
});


Vue.wechat.onMenuShareQZone({
title: title, // 分享标题
desc: desc, // 分享描述
link: link, // 分享链接
imgUrl: imgUrl, // 分享图标
success: function () {


},
cancel: function () {

}
});


Vue.wechat.onMenuShareWeibo({
title: title, // 分享标题
desc: desc, // 分享描述
link: link, // 分享链接
imgUrl: imgUrl, // 分享图标
success: function () {


},
cancel: function () {

}
});

})

}

export { wxShare }







